import React, { useState } from 'react';
import "../index.css";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText
} from 'reactstrap';

const Navbar1 = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar color="light" light expand="md" className="c1">
        <NavbarBrand href="https://maxibesso11.github.io/cri-test" style={{fontWeight:"700",fontSize:"25px"}}>Cri Cri </NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink href="#products" style={{fontWeight:"600"}}>Nuestros productos</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#whoareus" style={{fontWeight:"600"}}>Â¿Quienes somos?</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#goals" style={{fontWeight:"600"}}>Nuestros Objetivos</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#contact" style={{fontWeight:"600"}}>Contactenos</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
}

export default Navbar1;